
import { defineComponent } from "vue";

export default defineComponent({
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data() {
    return {
      players: new Array<string>(),
      pairs: new Array<string>(),
    };
  },
  methods: {
    addPlayer: function () {
      const name = prompt("Nombre del jugador");
      if (!name || name.length == 0) {
        this.addPlayer();
      } else {
        if (typeof name == "string") this.players.unshift(name);
        this.makePairs(this.players);
      }
    },
    makePairs: function (array: string[]) {
      this.pairs = [];
      const pairs = array
        .map((value) => ({ value, sort: Math.random() }))
        .sort((a, b) => a.sort - b.sort)
        .map(({ value }) => value);
      console.log("pairs", pairs);
      while (pairs.length > 0) {
        const pairNames = (pairs[0] || "") + " - " + (pairs[1] || "");
        pairs.splice(0, 1);
        pairs.splice(0, 1);
        console.log(pairNames);
        this.pairs.push(pairNames);
      }
    },
  },
});
